import { Component, Vue } from 'vue-property-decorator';
import  '../assets/js/dashboard/dash_1.js'
import {HttpService} from "@/services/ApiService";
import {DashboardInterface} from "@/models/dashboard";

@Component
export default class Dashboard extends Vue {
  public isLoading = true

  public dash: DashboardInterface | null = null;

  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';

  public options= {
      chart: {
        id: 'usuarios'
      },
      xaxis: {
        categories: ['usuarios', 'repartidores', 'todi']
      }
    }

  public usuarios: any = [{
      name: 'usuarios',
      data: [30, 40, 45]
    }]

  public  estados = [44, 55, 13, 43, 22, 13, 43, 22];

  public chartOptions = {
    chart: {
      type: 'pie',
      id: 'estados'
    },
    colors: ['#1cae0d', '#5ce238', '#31a7c3', '#357aab',
      '#025399', '#0119a2', '#310cb8', '#d00e0e'],
    labels: ['completado', 'en proceso', 'iniciado', 'en lugar', 'en camino', 'confirmado', 'recibido', 'cancelado'],
    responsive: [{
      breakpoint: 480,
      options: {
        chart: {
          width: 200,
          height: 250
        },
        legend: {
          position: 'bottom'
        }
      }
    }]
  };


  async getDashboard() {
    await HttpService.get(`dashboard?append=estado` ,
        this.setDashboard)
    this.isLoading = false;
  }

  setDashboard(data: any){
    console.log(data)
    this.dash = data;
    this.estados = this.dash!.data.estados;
    this.usuarios = [{
      name: 'usuarios',
      data: this.dash!.data.usuarios
    }]

  }
  created(){
    this.isLoading = false;
    //this.getDashboard();
  }


}
